export default {
	data() {
		return {
			chatlist:''
		}
	},
	methods: {
		/* 点击进入另外的页面 */
		chatadd(){
			uni.navigateTo({
			    url: '../../component/chatadd/chatadd'
			});
		}
	},
	created() {
		let self=this
		uni.request({
			url:'http://localhost:10086/chat',
			success(res) {
				
				self.chatlist=res.data.data.reverse()
				// console.log('chat',res.data.data.reverse())
			}
		})
	}
}
